.container {
    max-width: 1280px;
    width: 100%;
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
}

.flexRow, .flexColumn, .flexWrap, .flexGrid { 
    display: flex;
}

.flexColumn {
    flex-direction: column;
}

.flexWrap, .flexGrid {
    flex-wrap: wrap;
}

.flexCell {
    padding: 0 15px;
    box-sizing: border-box;
}

.marginIndents {
    margin: 0 -15px;
}